		getImagesByCategoryId(categoryId) {
			let images = this.getImages();
			let categoryImages = new Array();
			for (let i=0; i<images.length; i++) {
				if (images[i].categories != null) {
					for (let j=0; j<images[i].categories.length; j++) {
						if (images[i].categories[j]==categoryId) {
							categoryImages.push(images[i]);
						}
					}
				}
			}
			return categoryImages;
		},

		changeCurrentImageCategory() {
			this.currentImages = this.getImagesByCategoryId(this.currentImageCategoryId);
		}
	},
	data() {
		return {
