<template>
  <div>

    
    <div v-if="vm.currentTab=='IMPORT_EXPORT'">

      <!--
      <h2>Export game</h2>

      <br>

      <div class="home">
        <p>Soon you will be able to export your games to HTML and embed it in any website.</p>
      </div>
      -->

      
      <h2>Export JSON</h2>
      <div><button type="button" class="btn btn-primary" v-on:click="vm.exportDecycled();"><i class="fas fa-file-export"></i> Export</button></div>
      <div v-if="vm.gameParamsJsonDecycled != null">
        <br>
        <div>JSON exported:</div>
        <div><textarea style="width: 800px; font-size:12px" rows="10" v-model="vm.gameParamsJsonDecycled"></textarea></div>
      </div>
      <br>

      <h2>Import JSON</h2>
      <div>Import JSON:</div>
      <div><textarea style="width: 800px; font-size:12px" rows="10" v-model="vm.newGameParamsJson"></textarea></div>
      <div><button type="button" class="btn btn-primary" v-on:click="vm.importDecycled();"><i class="fas fa-file-import"></i> Import</button></div>
      <br>

      <h2>Generate source code</h2>
      <div><button type="button" class="btn btn-primary" v-on:click="vm.generateSourceCode();">Generate source code</button></div>
      <div>
        <br>
        <div>Javascript source code generated:</div>
        <div><textarea style="width: 800px; font-size:12px" rows="30" v-model="vm.sourceCode"></textarea></div>
      </div>
      <br>

      <!--
      <h2>Export game</h2>
      <div><button type="button" class="btn btn-primary" v-on:click="exportGame();">Export game</button></div>
      <div>
        <br>
        <div>Exported game:</div>
        <div><textarea style="width: 800px; font-size:12px" rows="30" v-model="exportedGame"></textarea></div>
      </div>
      <br>
      -->
      
      
      
    </div>
    

  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ImportExport',
  props: {
    vm: Object
  },
  methods: {
    
    exportGame: function () {

      this.loading = true;
      this.errorMessage = null;

      var data = {
        gameId: this.vm.gameId
      };

      let that = this;

      let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
      let url = apiBaseUrl + 'exportGame';

      const config = {
        //headers: { 
        //	"Authorization": "Bearer " + getCookie("sessionId")
        //},
        withCredentials: true
      };

      axios
        .post(url, data, config)
        .then(function (response) { // eslint-disable-line no-unused-vars
          that.exportedGame = response.data;
          that.loading = false;
        })
        .catch(function (error) {
          that.errorMessage = "Error: " + error;
          that.loading = false;
        });
    }
    
  },
  data() {
    return {
      exportedGame: ""
    }
  }
}
</script>

