<template>

	<div v-if="vm.currentTab=='SAVE'">
		<h2>Save Online</h2>
		<table v-show="authenticated">
			<tr>
				<td>Game id</td>
				<td>{{vm.gameId}}</td>
			</tr>
			<tr>
				<td>Name</td>
				<td><input type="text" v-model="vm.gameName"></td>
			</tr>
			<tr>
				<td>Description</td>
				<td><textarea v-model="vm.gameDescription"></textarea></td>
			</tr>
			<!--
			<tr>
				<td>thumbnail</td>
				<td><input type="text" v-model="vm.gameThumbnail"></td>
			</tr>
			-->
			<!--
			<tr>
				<td>sessionId</td>
				<td>{{vm.sessionId}}</td>
			</tr>
			-->
		</table>
		<br>
		<button v-show="authenticated" type="button" v-on:click="saveGame()" :disabled="loading" class="btn btn-primary"><i class="fas fa-save"></i> Save Online</button>
		<p v-show="!authenticated">You need to <router-link to="/sign-in">sign-in</router-link> to save your games.</p>

		<div v-if="loading">
          <Spinner></Spinner>
        </div>

		<div v-if="message != null">
			<br><div class="alert alert-primary" role="alert" style="display:inline-block;">{{ message }}</div>
		</div>

		<div v-if="errorMessage != null">
			<br><div class="alert alert-danger" role="alert" style="display:inline-block;">{{ errorMessage }}</div>
		</div>
	</div>

</template>

<script>
import axios from 'axios';
import {getCookie} from '@/lib/mmgUtil.js';
import Spinner from '@/components/Spinner.vue';

export default {
  name: 'SaveOnline',
  components: {
    Spinner
  },
  props: {
    vm: Object
  },
  methods: {

	saveGame: function () {
		this.loading = true;
		this.message = null;
		this.errorMessage = null;

		this.vm.exportDecycled();

		var data = {
			id: this.vm.gameId,
			name: this.vm.gameName,
			description: this.vm.gameDescription,
			thumbnail: this.vm.gameThumbnail,
			templateCode: "action",
			templateVersion: this.vm.environment.templateVersion,
			paramsJson: this.vm.gameParamsJsonDecycled
		};

		let that = this;

		let apiBaseUrl = process.env.VUE_APP_API_BASE_URL;
		let url = apiBaseUrl + 'games';

		const config = {
			//headers: { 
			//	"Authorization": "Bearer " + getCookie("sessionId")
			//},
			withCredentials: true
		};

		axios
			.post(url, data, config)
			.then(function (response) { // eslint-disable-line no-unused-vars

				that.vm.gameId = response.data.id;

				that.message = "Game saved!";
				that.loading = false;
			})
			.catch(function (error) {
				that.errorMessage = "Error: " + error;
				that.loading = false;
			});
		
	}

  },
  data() {
    return {
		loading: false,
		message: null,
		errorMessage: null,
		authenticated: getCookie("authenticated")
    }
  },
  mounted () {
    
  }
}
</script>

